import React, { useState } from 'react';
import { Select, MenuItem, InputLabel, TextField } from '@material-ui/core';
import axios from 'axios';
import IconButton from '@material-ui/core/IconButton';
import SearchIcon from '@material-ui/icons/Search';
import Grid from '@material-ui/core/Grid';

const SearchField = () => {
  const [open, setOpen] = useState(false);
  const [state, setState] = useState({
    searchText: '',
    amount: 5,
    apiUrl: 'https://pixabay.com/api',
    apiKey: '16209559-d8a3895a46194b6395ededbf8',
    images: [],
  });

  function fetch() {
    console.log(state.images);

    if (state.searchText.length > 0 && state.amount > 0) {
      axios
        .get(
          `${state.apiUrl}?key=${state.apiKey}&q=${state.searchText}&image_type=photo&safesearch=true&per_page=${state.amount}`
        )
        .then((res) => setState({ ...state, images: res.data.hits }))
        .catch((err) => console.log(err));
    } else {
      setState({
        ...state,
        images: [],
      });
    }
  }

  const onTextChange = (e) => {
    if (state.searchText.length === 0) {
      setState({
        ...state,
        images: [],
      });
    }
    setState({ ...state, [e.target.name]: e.target.value });
  };

  function onAmountChange(e) {
    const { name, value } = e.target;
    setState((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  }

  return (
    <div>
      <div className='searchBox'>
        <TextField
          className='text-field'
          name='searchText'
          onChange={onTextChange}
          value={state.searchText}
          label='Image Name'
        />

        <div>
          <InputLabel>Count</InputLabel>
          <Select
            name='amount'
            open={open}
            onClose={() => setOpen(false)}
            onOpen={() => setOpen(true)}
            value={state.amount}
            onChange={onAmountChange}
          >
            <MenuItem value={0}>
              <em>0</em>
            </MenuItem>
            <MenuItem value={5}>5</MenuItem>
            <MenuItem value={10}>10</MenuItem>
            <MenuItem value={15}>15</MenuItem>
            <MenuItem value={20}>20</MenuItem>
          </Select>
        </div>

        <IconButton className='search-btn' onClick={fetch}>
          <SearchIcon />
        </IconButton>
      </div>
      <Grid container spacing={3}>
        {state.images.map((image) => {
          return (
            <Grid item xs={12} sm={4} key={image.id}>
              <img src={image.largeImageURL} alt='finding images' />
            </Grid>
          );
        })}
      </Grid>
    </div>
  );
};

export default SearchField;
